/**
 * @description SSR
 * @date 30/09/2022
 */
export const isSSR = (function () {
  try {
    return (
      typeof window === 'undefined' ||
      !window.navigator ||
      /ServerSideRendering|^Deno\//.test(window.navigator.userAgent)
    )
  } catch (e) {
    return true
  }
})()

/**
 * @description href
 * @date 16/11/2022
 * @param {string} str
 * @return {*}  {string}
 */
export const getHref = (str: string): string => {
  const value = /(?<=<a.*href=").*(?=")/.exec(str)
  return value?.length ? value[0] : ''
}

/**
 * @description 
 * @date 16/11/2022
 * @param {string} str
 * @return {*}  {string}
 */
export const getValue = (str: string): string => {
  const value = /(?<=">).*(?=<\/a)/.exec(str)
  return value?.length ? value[0] : ''
}

/**
 * @description id
 * @date 16/11/2022
 * @param {string} str
 * @return {*}  {string}
 */
export const getID = (str: string): string => {
  const value = str.match(/(?<=<h4.*id=").*(?="\sstyle)/)
  return value?.length ? value[0].replace(/-/g, ' ') : ''
}

/**
 * @description path
 */
export const getPathname = (path: string): string => {
  return path.split('/')[2]
}

/**
 * @description 
 * @date 15/10/2023
 * @return {*}  {string}
 */
export const randomString = () => Math.random().toString(36).slice(2)

/**
 * @description 
 * @date 15/10/2023
 * @return {*}  {string}
 */
export const randomColor = () =>
  '#' +
  Math.floor(Math.random() * 0xffffff)
    .toString(16)
    .padEnd(6, '0')

/**
 * @description url
 * @date 15/10/2023
 * @return {*}  {object}
 */
export const parseQuery = (url: string) => {
  const q = {}
  url.replace(/([^?&=]+)=([^&]+)/g, (_, k, v) => (q[k] = v))
  return q
}
